import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LabelLotsComponent } from './label-lots.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { DragDropModule } from '@angular/cdk/drag-drop';

import { PrintOneColComponent } from './print-one-col/print-one-col.component';
import { PrintFourColComponent } from './print-four-col/print-four-col.component';
import { PrintFiveColComponent } from './print-five-col/print-five-col.component';
import { DetailComponent } from './detail/detail.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PrintTwoColComponent } from './print-two-col/print-two-col.component';
import { PrintThreeColComponent } from './print-three-col/print-three-col.component';
const routes: Routes = [
  {
    path: '',
    component: LabelLotsComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  },
  {
    path: 'print-two-col',
    component: PrintTwoColComponent
  },
  {
    path: 'print-three-col',
    component: PrintThreeColComponent
  },
  {
    path: 'print-one-col',
    component: PrintOneColComponent
  },
  {
    path: 'print-four-col',
    component: PrintFourColComponent
  },
  {
    path: 'print-five-col',
    component: PrintFiveColComponent
  },
]
@NgModule({
  declarations: [
    LabelLotsComponent,
    DetailComponent,
    PrintTwoColComponent,
    PrintThreeColComponent,
    PrintOneColComponent,
    PrintFourColComponent,
    PrintFiveColComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule, 
    DragDropModule,
    FormsModule,
    ReactiveFormsModule
  ]
})
export class LabelLotsModule { }
